.accordion {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #eceff1;
}

.accordionBtn {
  background-color: transparent;
  cursor: pointer;
  padding: 24px 40px;
  display: flex;
  justify-content: flex-start;
  border: none;
  outline: none;
  position: relative;
}

.accordionBtn::before {
  content: "";
  width: 14px;
  height: 2px;
  background: #333;
  position: absolute;
  top: calc(50% - 1px);
  left: 0;
  display: block;
  transition: all 0.3s;
}

.accordionBtn::after {
  content: "";
  width: 2px;
  height: 14px;
  background: #333;
  position: absolute;
  top: calc(50% - 7px);
  left: 6px;
  display: block;
  transition: all 0.3s;
}

.active .accordionBtn::after {
  transform: rotate(90deg) translateX(-1px);
}

//.active .accordionBtn::before {
//  transform: rotate(180deg);
//}

.title {
  font-weight: 800;
  font-size: 16px;
  line-height: 20px;
  text-align: left;
  color: #1a1a1a;
}

.content {
  padding-left: 40px;
  max-height: 0;
  background-color: white;
  overflow: hidden;
  transition: max-height 0.3s ease;
}

.active .content {
  max-height: 100vh;
}

.text {
  padding-bottom: 24px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
}
