@import "../../constants/colors";

.wrapper {
  position: fixed;
  inset: 0;
  width: 100vw;
  height: 100vh;
  border: none;
  background: rgba($black, 0.4);
  backdrop-filter: blur(5px);
  z-index: 5;
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 450px;
  background: $white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08), 0 20px 32px rgba(0, 0, 0, 0.24);
  border-radius: 20px;
  padding: 30px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  &Medium {
    width: 1000px;
  }
  &Largest {
    width: 80%;
  }
}
